<template>
  <div class="p-4 max-w-xl mx-auto text-center">
    <h1 class="text-2xl font-bold mb-4">üõ°Ô∏è Ï†ïÎ≥¥ Î≥¥Ïïà ÎßàÎ∏î</h1>

    <div class="grid-board">
      <div
        v-for="(label, visualIndex) in 25"
        :key="visualIndex"
        class="tile"
        :class="{
          'active': visualIndex === tileOrder[position],
          'empty': !tileIndexMap.has(visualIndex)
        }"
      >
        <span v-if="tileIndexMap.has(visualIndex)">
          {{ board[tileIndexMap.get(visualIndex)] || visualIndex + 1 }}
        </span>
        <span v-if="visualIndex === tileOrder[position]" class="piece">üßç‚Äç‚ôÇÔ∏è</span>
      </div>
    </div>

    <div class="mt-4">
      <button class="bg-blue-600 text-white px-4 py-2 rounded" @click="rollDice" :disabled="rolling">
        üé≤ Ï£ºÏÇ¨ÏúÑ Íµ¥Î¶¨Í∏∞
      </button>
      <div v-if="dice !== null">Ï£ºÏÇ¨ÏúÑ: {{ dice }} üé≤</div>
      <p class="mt-2">ÌòÑÏû¨ ÏúÑÏπò: {{ tileOrder[position] + 1 }}Î≤à Ïπ∏</p>
      <p class="mt-1">Î≥¥Ïïà Ï†êÏàò: {{ score }}Ï†ê</p>
    </div>

    <div v-if="eventText" class="bg-yellow-100 p-4 mt-4 rounded shadow">
      <p>{{ eventText }}</p>
      <button v-if="showNext" class="mt-4 bg-green-500 text-white px-4 py-2 rounded" @click="nextTurn">Îã§Ïùå ÌÑ¥</button>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue'

// Ïã§Ï†ú Î≥¥Îìú ÏàúÏÑúÎåÄÎ°ú Ïù¥ÎèôÌï† Ïπ∏Îì§Ïùò Ïù∏Îç±Ïä§
const tileOrder = [
  0, 1, 2, 3, 4,
  9, 14,
  19, 24,
  23, 22, 21, 20,
  15, 10,
  5, 6, 7, 8
] // Ï¥ù 20Ïπ∏

// ÏãúÍ∞ÅÏ†Å ÏúÑÏπòÏôÄ Ïã§Ï†ú Î≥¥Îìú Î∞∞Ïó¥Ïùò Ïù∏Îç±Ïä§Î•º Ïó∞Í≤∞
const tileIndexMap = new Map()
tileOrder.forEach((visualIdx, realIdx) => {
  tileIndexMap.set(visualIdx, realIdx)
})

const board = [
  'Ï∂úÎ∞ú',
  'ÎØ∏ÏÖò: ÎπÑÎ∞ÄÎ≤àÌò∏',
  'ÏúÑÍ∏∞: ÌîºÏã±',
  'ÏïÑÏù¥ÌÖú: Î∞±Ïã†',
  'ÎØ∏ÏÖò: Ìï¥Ïª§ ÌÄ¥Ï¶à',
  'ÏúÑÍ∏∞: ÏïÖÏÑ±ÏΩîÎìú',
  'ÎØ∏ÏÖò: ÏôÄÏù¥ÌååÏù¥',
  'ÏïÑÏù¥ÌÖú: VPN',
  'ÏúÑÍ∏∞: Î¨¥Îã®Ï†ëÍ∑º',
  'ÎØ∏ÏÖò: Ïù¥Î©îÏùº',
  'ÏïÑÏù¥ÌÖú: Î∞©ÌôîÎ≤Ω',
  'ÏúÑÍ∏∞: Ï†ïÎ≥¥Ïú†Ï∂ú',
  'ÎØ∏ÏÖò: Ï†ÄÏû•Îß§Ï≤¥',
  'ÏïÑÏù¥ÌÖú: Ïù¥Ï§ëÏù∏Ï¶ù',
  'ÏúÑÍ∏∞: Ìï¥ÌÇπ Í≥µÍ≤©',
  'ÎØ∏ÏÖò: Îç∞Ïù¥ÌÑ∞ ÏïîÌò∏Ìôî',
  'ÏïÑÏù¥ÌÖú: Î≥¥Ïïà Ìà¥ÌÇ∑',
  'ÎØ∏ÏÖò: Í∞úÏù∏Ï†ïÎ≥¥ Î≥¥Ìò∏',
  'ÏúÑÍ∏∞: Ìå®Ïä§ÏõåÎìú Ïú†Ï∂ú',
  'ÎØ∏ÏÖò: Î°úÍ∑∏Ïù∏ Î≥¥Ïïà'
]

const position = ref(0)
const dice = ref(null)
const score = ref(0)
const eventText = ref('')
const showNext = ref(false)
const rolling = ref(false)

function rollDice() {
  if (rolling.value) return
  rolling.value = true
  dice.value = Math.floor(Math.random() * 6) + 1
  setTimeout(() => {
    movePlayer(dice.value)
    rolling.value = false
  }, 1000)
}

function movePlayer(steps) {
  position.value = (position.value + steps) % board.length
  const current = board[position.value] || 'ÏùºÎ∞ò Ïπ∏'

  if (current.includes('ÎØ∏ÏÖò')) {
    score.value += 10
    eventText.value = `${current} ‚úî Ï†ïÎãµ! +10Ï†ê`
  } else if (current.includes('ÏïÑÏù¥ÌÖú')) {
    eventText.value = `${current} üéÅ ÏïÑÏù¥ÌÖú ÌöçÎìù! (Î≥¥ÏïàÏóê ÎèÑÏõÄÎê®)`
  } else if (current.includes('ÏúÑÍ∏∞')) {
    score.value -= 10
    eventText.value = `${current} ‚ùå Î≥¥Ïïà Ïã§Ìå®! -10Ï†ê`
  } else {
    eventText.value = `${current} üôå Ïù¥Îèô ÏôÑÎ£å`
  }

  showNext.value = true
}

function nextTurn() {
  eventText.value = ''
  dice.value = null
  showNext.value = false
}
</script>

